import ca from "element-ui/src/locale/lang/ca";
import { isEmptyStr, isNull } from "@/utils/util";

const FormValidators = {
  /*  */
  number(rule, value, callback) {
    //
    if (isEmptyStr(value) || isNull(value)) {
      callback();
      return;
    }

    let reg = /^[0-9]+.?[0-9]*$/;
    if (!reg.test(value)) {
      callback(new Error("[" + rule.label + "]"));
    } else {
      callback();
    }
  },

  /*  */
  mobile(rule, value, callback) {
    //
    if (isEmptyStr(value) || isNull(value)) {
      callback();
      return;
    }

    let reg = /^[1][3456789][0-9]{9}$/;
    if (!reg.test(value)) {
      callback(new Error("[" + rule.label + "]"));
    } else {
      callback();
    }
  },

  /*  */
  noBlankStart(rule, value, callback) {},

  /*  */
  noBlankEnd(rule, value, callback) {},

  /*  */
  letterStartNumberIncluded(rule, value, callback) {
    //
    if ((isEmptyStr(value) || isNull(value)) && value !== " ") {
      callback();
      return;
    }

    let reg = /^[A-Za-z]+[A-Za-z\d]*$/;
    if (!reg.test(value)) {
      callback(new Error("[" + rule.label + "]"));
    } else {
      callback();
    }
  },

  /*  */
  noChinese(rule, value, callback) {
    //
    if (isEmptyStr(value) || isNull(value)) {
      callback();
      return;
    }

    let reg = /[\u4e00-\u9fa5]/;
    if (reg.test(value)) {
      callback(new Error("[" + rule.label + "]"));
    } else {
      callback();
    }
  },

  /*  */
  email(rule, value, callback) {
    //
    if (isEmptyStr(value) || isNull(value)) {
      callback();
      return;
    }

    let reg = /^([-_A-Za-z0-9.]+)@([_A-Za-z0-9]+\.)+[A-Za-z0-9]{2,3}$/;
    if (!reg.test(value)) {
      callback(new Error("[" + rule.label + "]"));
    } else {
      callback();
    }
  },

  /* URL */
  url(rule, value, callback) {
    //
    if (isEmptyStr(value) || isNull(value)) {
      callback();
      return;
    }

    let reg =
      /^([hH][tT]{2}[pP]:\/\/|[hH][tT]{2}[pP][sS]:\/\/)(([A-Za-z0-9-~]+)\.)+([A-Za-z0-9-~\/])+$/;
    if (!reg.test(value)) {
      callback(new Error("[" + rule.label + "]URL"));
    } else {
      callback();
    }
  },

  /*
  test(rule, value, callback) {
    if (value < 100) {
      callback(new Error('[' + rule.label + ']100'))
    } else {
      callback()
    }
  },
  */
};

export default FormValidators;
